/* Design inspiration:  https://dribbble.com/shots/1978243-Latest-News */
body, html {
  font-family: "Montserrat", sans-serif;
  background-color: #341cac;
}

ul,li {
  list-style-type: none;
  margin:3px;
  padding:0px;
}

p {
  font-weight: 200;
  opacity: .5;
}
.card {
  width: 280px;
  display: block;
  margin: 60px auto;
  box-shadow: 10px 5px 40px 20px #2d1896;
  transition: 0.25s
  
}

.card-header {
  max-height:300px;
  width: auto;
  padding: 0px;
  width: 100%;
  background-size: cover;
  color: #fff;
}

.card-detail-header {
  height: auto;
  width: 30%;
  position: relative;
  padding-right: 10px;
  /* background-size: cover; */
  color: #fff;
  float: left;
}

.card-header-title {
  text-transform: uppercase;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  margin: 0px;
  font-size: 2rem;
  width: 70%;
  white-space: normal;

}

.smaller-title {
  text-transform: uppercase;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  margin: 0;
  font-size: 1.5rem;
  float: left;
  padding: 10px;
  position: relative;
  width: 10%;
  list-style: none;
}

.card-body {
  padding: 15px;
  background-color: #fff;
  width: 100%;
}

.detail-card {
  width: 500px;
  display: block;
  margin: 60px auto;
  box-shadow: 10px 5px 40px 20px #2d1896;
  transition: 0.25s;
}
.detail-card-body {
  padding: 15px;
  background-color: #fff;
  width: 100%;
}

.detail-card-desc{
  position: relative; 
  width: 80%;
  padding:10px;
  overflow-y:scroll;
  white-space: normal;
}

.body-content {
  padding-bottom: 40px;
  font-size: 13px;
  line-height: 1.8;
}

body, html {
  overflow-x: hidden;
}

.app-header {
  display: block;
  padding: 30px;
}

.app-title {
  display: inline-block;
  width: 100%;
  margin: 80px 0 0 0;
  float: left;
  position: relative;
  color: #fff;
}
@media (min-width: 768px) {
  .app-title {
    width: 20%;
    margin: 280px 0 0 0;
  }
}
.app-title h1 {
  font-size: 52px;
  margin: 0;
}
.app-title p {
  font-weight: 200;
  opacity: 0.5;
}

.app-title-content {
  position: relative;
  top: 50%;
  left: 50%;
  width: 100%;
  text-align: center;
  transform: translate(-50%, -50%);
}
@media (min-width: 768px) {
  .app-title-content {
    width: auto;
    position: absolute;
    text-align: left;
  }
}

.app-card-list {
  display: inline-block;
  width: 100%;
  float: right;
  margin: 0;
  min-height: 75vh;
  white-space: normal;
  overflow-x: scroll;
  margin-top: -70px;
}
.app-card-list::-webkit-scrollbar {
  display: none;
}
@media (min-width: 768px) {
  .app-card-list {
    width: 80%;
    white-space: nowrap;
    margin-top: 0;
  }
}
.app-card-list .card {
  margin: 60px 30px;
  display: inline-block;
  position: relative;
}
.app-card-list .card .card-body {
  white-space: unset;
}

.app-card-detail {
 position: relative;
 min-height: 100px;
 max-height: 150px;
 overflow-y:scroll;
 white-space: normal;
}
.developer-link {
  display: inline-block;
  padding-top: 40px;
  color:lightgray;
}